import React from 'react';

const Footer = () => {
  return (
    <footer style={styles.footer}>
      <div style={styles.container}>
        <div style={styles.column}>
          <img src={`${process.env.PUBLIC_URL}/img/logos.png`} alt="El Chel Logo" style={styles.logo} />
          <p style={styles.slogan}>La Casa de Todos</p>
        </div>
        <div style={styles.column}>
          <h3 style={styles.title}>Conócenos</h3>
          <ul style={styles.list}>
            <li>Zona de Cobertura</li>
            <li>Contacto</li>
            <li>Información Legal</li>
            <li>Términos y Condiciones</li>
            <li>Política de Privacidad</li>
          </ul>
        </div>
        <div style={styles.column}>
          <h3 style={styles.title}>Redes sociales</h3>
          <ul style={styles.list}>
            <li>Instagram</li>
            <li>Facebook</li>
            <li>Twitter</li>
          </ul>
        </div>
        <div style={styles.column}>
          <h3 style={styles.title}>Mi cuenta</h3>
          <ul style={styles.list}>
            <li>Pedir</li>
            <li>Puntos El Chel</li>
            <li>Iniciar sesión</li>
          </ul>
        </div>
      </div>
    </footer>
  );
};

const styles = {
  footer: {
    backgroundColor: '#FF5722', // Naranja
    color: '#FFFFFF', // Texto blanco
    padding: '20px 30px',
    textAlign: 'left',
    fontFamily: 'Arial, sans-serif',
    overflowX: 'hidden', // Previene desbordamiento horizontal
  },
  container: {
    display: 'flex',
    flexWrap: 'wrap', // Permite que las columnas se ajusten en pantallas pequeñas
    justifyContent: 'space-between',
    alignItems: 'flex-start',
    maxWidth: '1200px',
    margin: '0 auto',
    gap: '20px', // Espaciado entre columnas
  },
  column: {
    flex: '1 1 250px', // Mínimo de 250px para evitar columnas muy pequeñas
    marginBottom: '20px', // Espaciado entre columnas en móviles
  },
  title: {
    fontSize: 'clamp(16px, 1.5vw, 18px)',
    fontWeight: 'bold',
    marginBottom: '10px',
  },
  list: {
    listStyle: 'none',
    padding: 0,
    margin: 0,
    fontSize: 'clamp(12px, 1.2vw, 14px)',
    lineHeight: '1.8',
  },
  logo: {
    width: 'clamp(100px, 10vw, 150px)',
    marginBottom: '10px',
  },
  slogan: {
    fontSize: 'clamp(12px, 1.2vw, 14px)',
    fontStyle: 'italic',
  },
};

export default Footer;
