import React, { useState, useEffect } from 'react';

const Banner = () => {
  const basePath = process.env.PUBLIC_URL || ''; // Ruta base dinámica

  const images = [
    `${basePath}/img/paquete.jpg`,
    `${basePath}/img/miercoles.jpg`,
    `${basePath}/img/paega.jpg`,
    `${basePath}/img/promos.png`,
  ];

  const [currentIndex, setCurrentIndex] = useState(0);

  // Cambia la imagen automáticamente cada 3 segundos
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
    }, 3000);

    return () => clearInterval(interval);
  }, [images.length]);

  return (
    <div style={styles.container}>
      {/* Lado Izquierdo: Texto */}
      <div style={styles.leftSide}>
        <h1 style={styles.text}>EL CHEL</h1>
        <p style={styles.subText}>EL ENCANTADOR DE ESTÓMAGOS</p>
      </div>

      {/* Lado Derecho: Slider */}
      <div style={styles.rightSide}>
        <div style={styles.slider}>
          {images.map((image, index) => (
            <div
              key={index}
              style={{
                ...styles.slide,
                transform: `translateX(${(index - currentIndex) * 100}%)`,
                transition: 'transform 0.8s ease-in-out',
              }}
            >
              <img
                src={image}
                alt={`Promoción ${index + 1}`}
                style={styles.image}
              />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    flexWrap: 'wrap', // Asegura el ajuste en pantallas pequeñas
    gap: '20px',
    padding: '5%', 
    maxWidth: '100%',
    overflowX: 'hidden', // Previene desbordamiento horizontal
    margin: '0 auto',
  },
  leftSide: {
    flex: '1 1 300px',
    textAlign: 'center',
  },
  text: {
    fontSize: 'clamp(2rem, 5vw, 3rem)',
    fontWeight: 'bold',
    color: '#ff5722',
    margin: '0 0 10px 0',
  },
  subText: {
    fontSize: 'clamp(1rem, 3vw, 1.5rem)',
    color: '#333',
    margin: 0,
  },
  rightSide: {
    flex: '1 1 300px',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  slider: {
    position: 'relative',
    width: '100%',
    maxWidth: '500px',
    aspectRatio: '16 / 9',
    overflow: 'hidden',
    borderRadius: '10px',
  },
  slide: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  image: {
    width: '100%',
    height: '100%',
    objectFit: 'cover',
  },
};

export default Banner;
