import React from 'react';
import '@fontsource/changa';
import '@fontsource/poppins';
import { Link, useLocation } from 'react-router-dom';

const Navbar = () => {
  const location = useLocation();
  const base = process.env.PUBLIC_URL || ''; // Ruta base para GitHub Pages o local

  // Lista de rutas y sus nombres
  const navItems = [
    { to: '/sucursales', label: 'Sucursales' },
    { to: '/menu', label: 'Menú' },
    { to: '/nosotros', label: 'Nosotros' },
    { to: '/contacto', label: 'Facturación' },
  ];

  return (
    <nav style={styles.navbar}>
      <div style={styles.logoContainer}>
        <Link to={`${base}/`}>
          <img
            src={`${process.env.PUBLIC_URL}/img/logos.png`}
            alt="El Chel Logo"
            style={styles.logoImage}
          />
        </Link>
      </div>
      <ul style={styles.navLinks}>
        {navItems.map((item, index) => (
          <li key={index} style={styles.navItem}>
            <Link
              to={`${base}${item.to}`}
              style={
                location.pathname === `${base}${item.to}`
                  ? { ...styles.link, ...styles.activeLink }
                  : styles.link
              }
            >
              {item.label}
            </Link>
          </li>
        ))}
      </ul>
    </nav>
  );
};

const styles = {
  navbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: '10px 20px',
    backgroundColor: 'white',
    borderBottom: '3px solid #e76f51',
    flexWrap: 'wrap', // Permite que se ajuste en pantallas pequeñas
  },
  logoContainer: {
    display: 'flex',
    alignItems: 'center',
  },
  logoImage: {
    height: '80px', // Tamaño ajustado para pantallas pequeñas
    width: 'auto',
  },
  navLinks: {
    display: 'flex',
    flexWrap: 'wrap', // Permite la caída del menú en pantallas chicas
    listStyle: 'none',
    margin: 0,
    padding: 0,
    gap: '15px',
    justifyContent: 'center',
    flex: 1,
  },
  navItem: {
    margin: '5px 0', // Espaciado adicional en móviles
  },
  link: {
    color: 'black',
    textDecoration: 'none',
    fontSize: 'clamp(14px, 2vw, 18px)', // Tamaño dinámico del texto
    textTransform: 'uppercase',
    fontFamily: 'Poppins, sans-serif',
    transition: 'color 0.3s, border-bottom 0.3s',
  },
  activeLink: {
    color: '#e76f51',
    borderBottom: '3px solid #e76f51',
  },
};

export default Navbar;
