import React from 'react';

const BannerPromocional = () => {
  return (
    <section style={styles.banner}>
      <div style={styles.textContainer}>
        <h2 style={styles.mainText}>¡EL CHEL MÁS CERCA DE TI!</h2>
        <p style={styles.subText}>Escríbenos y haz tu pedido para llevar o a domicilio</p>
        <a
          href="https://wa.me/7771861658"
          target="_blank"
          rel="noopener noreferrer"
          style={styles.callToAction}
        >
          <img
            src={`${process.env.PUBLIC_URL}/img/Whatsapp.jpg`}
            alt="WhatsApp"
            style={styles.icon}
          />
          LLÁMANOS YA
        </a>
      </div>
      <div style={styles.imageContainer}>
        <img
          src={`${process.env.PUBLIC_URL}/img/promo.png`}
          alt="Promoción"
          style={styles.image}
        />
      </div>
    </section>
  );
};
const styles = {
  banner: {
    display: 'flex',
    flexDirection: 'row', // Mantiene texto a la izquierda y la imagen a la derecha
    justifyContent: 'space-between', // Espacio máximo entre elementos
    alignItems: 'center',
    padding: '1rem 2rem', // Padding reducido
    background: 'linear-gradient(to bottom, #ff9800, #ff6f00)',
    boxShadow: '0px 2px 5px rgba(0, 0, 0, 0.2)',
    fontFamily: '"Arial", sans-serif',
    width: '100%',
    margin: '0',
    height: '350px', // Aumentar altura del banner
  },
  textContainer: {
    flex: '1', // Ocupa espacio restante a la izquierda
    textAlign: 'left', // Alineado a la izquierda
    color: '#fff',
    padding: '0 1rem',
  },
  mainText: {
    fontSize: 'clamp(1.8rem, 3.5vw, 2.8rem)',
    fontWeight: 'bold',
    marginBottom: '0.5rem',
  },
  subText: {
    fontSize: 'clamp(1rem, 2vw, 1.5rem)',
    marginBottom: '1rem',
  },
  callToAction: {
    display: 'inline-flex',
    alignItems: 'center',
    backgroundColor: '#25d366',
    color: '#fff',
    textDecoration: 'none',
    padding: '0.6rem 1.2rem',
    borderRadius: '5px',
    fontWeight: 'bold',
    fontSize: 'clamp(0.8rem, 1.5vw, 1rem)',
    boxShadow: '0 2px 3px rgba(0, 0, 0, 0.2)',
    gap: '0.3rem',
  },
  icon: {
    width: '1rem',
    height: '1rem',
    marginRight: '0.5rem',
  },
  imageContainer: {
    flex: '0 1 50%', // Ajusta la imagen a un 40% del ancho total
    display: 'flex',
    justifyContent: 'flex-end', // La imagen se acerca más a la izquierda
    alignItems: 'center',
    paddingRight: '7%', // Mueve la imagen un poco a la izquierda
  },
  image: {
    width: '100%', // Imagen más grande
    maxWidth: '550px', // Límite del tamaño máximo de la imagen
    height: 'auto',
    objectFit: 'cover',
    borderRadius: '10px',
  },
};

export default BannerPromocional;

